.NewToDoButton {
  position: absolute;
  right: 0;
  bottom: -50px;
  width: 40px;
  height: 40px;
  border: none;
  background-color: var(--third-color);
  color: var(--main-bg-color);
  border-radius: 50%;
  box-shadow: 0px 0px 10px 0px rgb(24 24 24 / 50%);
  cursor: pointer;
  z-index: 3;
}
